  Component.options.__i18n.push('{"de":{"title":"Emissionen über die Zeit in Europa","sector_agriculture":"Landwirtschaft","sector_all":"alle Sektoren","sector_energy":"Energiesektor","sector_waste":"Abfall","sector_transport":"Verkehr","sector_industry":"Industrie","sector_other":"Sonstige","description_all":"2017 wurden durch die EU 4168 millionen Tonnen Treibhausgas-Emissionen produziert. Damit liegt die EU weltweit auf Platz 3, hinter den USA und China. Im Ländervergleich unterscheiden sich die einzelnen Sektoren oft in ihren Anteilen.","description_agriculture":"In der Landwirtschaft wird der größte Anteil an Nicht-CO₂-Gasen erzeugt. NO₂ entsteht bei der Düngung der Felder. CH₄ (Methan) bei der Verdauung in Nutztieren (Kühe, Schweine, Schafe, …) und beim Reisanbau. Dieser Sektor hat einen Gesamtanteil in der EU von 10%.","description_energy":"Bei der Stromerzeugung werden gerade durch die Verbrennung von Braunkohle viele CO₂-Emissionen freigesetzt, aber auch bei der Herstellung von Treibstoff und Benzin entstehen Emissionen. Dies ist mit 28% Anteil in der EU einer der größten Sektoren.","description_waste":"Abfall beinhaltet alles rund um die Müll- und Abwasserverarbeitung (Entsorgung, Kompostierung, Verbrennung, Wasseraufbereitung/-klärung. Auf diesen Sektor entfallen EU-weit 3%.","description_transport":"Verkehr umfasst alle Inlandsflüge sowie Auto-, Bahn- und Lastwagenverkehr. Dieser Sektor hat einen Gesamtanteil in der EU von 21%.","description_industry":"Zum Industrie-Sektor zählen alle Emissionen, die bei der Produktion von Gütern und Rohmaterialien entstehen. Dieser Sektor hat einen Gesamtanteil von 9%.","description_other":"Hierin sind verschiedene weitere Sektoren zusammengefasst, der Größte hiervon macht über 50% aus und umfasst die Emissionen von Gebäuden (Heizung, Abwasser, Strom in Wohnungen oder Geschäften). Der gesamte Sektor hat einen Gesamtanteil von 28% an allen EU-Emissionen.","million_tons":"mio. Tonnen","next_desc":"Zu den Auswirkungen erzähle ich dir jetzt mehr.","next_btn":"weiter"},"en":{"title":"Emissions Over Time in Europe","sector_agriculture":"Agriculture","sector_all":"All Sectors","sector_energy":"Energy","sector_waste":"Waste","sector_transport":"Transport","sector_industry":"Industry","sector_other":"Other","description_all":"In 2017 the EU produced 4168 million tons of greenhouse gases. This makes the EU the 3rd biggest global greenhouse gas emitter, after the USA and China. However, individual EU nations differ both in how much each sector contributes, and their overall contribution to total EU emissions.","description_agriculture":"In agriculture most emissions come from non-CO₂ gases. NO₂ is produced by the fertilisation of fields. CH₄ (methane) is produced by the digestive systems of livestock (cows, pigs, sheep, etc.) and by rice fields. This sector is responsible for 10% of the total emissions in the EU.","description_energy":"When it comes to energy generation, the burning of brown coal in particular contributes heavily to CO₂ emissions - the use of fuels such as petrol doesn\u0027t help either. This is one of the biggest sectors in terms of emissions, contributing 28% of the EU\u0027s total CO₂ output.","description_waste":"Waste includes everything around waste and waste water processing (disposal, composting, incineration and water treatment). This sector is responsible for 3% of the EU total emissions.","description_transport":"Transport covers all inland traffic by car, train and truck. This sector is responsible for 21% of all EU emissions.","description_industry":"The industry sector covers emissions resulting from the production of goods and raw materials. This sector is responsible for 9% of total EU emissions.","description_other":"This represents the aggregation of various other sectors. The largest of these is the emissions from housing (heating, sewage treatment, home electrification, businesses) which make up 50% of this sector. The whole sector contributes to 28% of 