module.exports = function (Component) {
  Component.options.__i18n = Component.options.__i18n || []
  Component.options.__i18n.push('{"de":{"title":"Zero Waste - Null Müll Regeln","rule_1":"Verzichte auf Dinge, die du nicht wirklich benötigst. Frage dich vor einer neuen Anschaffung, ob du sie wirklich brauchst und wie lange sie für dich nützlich sein wird.","rule_2":"Reduziere, was du brauchst. Sieh dich zu Hause und in deinem Alltag um und finde Dinge, die du nicht wirklich brauchst oder mehrfach besitzt. Spende oder verkaufe diese Dinge.","rule_3":"Nutze wiederverwendbare Dinge. Vermeide Einwegartikel und versuche, sie in deinem Alltag mit langlebigen Alternativen zu ersetzen oder zumindest Alternativen, die z.B. kompostierbar sind.","rule_4":"Recycle, was du nicht mit den ersten drei Regeln vermeiden kannst. Informiere dich, wie in deiner Gegend Müll getrennt wird und halte dich an diese Regeln.","rule_5":"Kompostiere den Rest. Mit den obigen Regeln sollte der Großteil des Mülls behandelt worden sein. Was jetzt noch übrig ist, sollten Essenreste und andere kompostierbare Abfälle sein.","act_1":"#1 Verzichten","act_2":"#2 Reduzieren","act_3":"#3 Wiederverwenden","act_4":"#4 Wiederverwerten","act_5":"#5 Kompostieren","subtitle_msg":"Versuch dir diese Regeln zu merken und sie in deinen Alltag anzuwenden.","next_btn":"Übersicht Auswirkungen","next_examples":"Übersicht Handeln"},"en":{"title":"Zero Waste Rules","rule_1":"Refuse what you do not need. Ask yourself if you really need it before buying and how long it will actually be useful to you.","rule_2":"Reduce what you do need. Examine your home and lifestyle to identify things that you do not actually need such as duplicates or unused items in the kitchen or wardrobe. Donate the extra items to secondhand stores or sell online!","rule_3":"Reuse by using reusables. Avoid single-use items with long-lasting alternatives that either A) will last into a foreseeable future (think stainless steel or glass) or B) are compostable.","rule_4":"Recycle what you cannot refuse, reduce or reuse. Research your local recycling policies and follow their rules.","rule_5":"Rot (a.k.a. compost the rest). At this point, you should have taken care of the vast majority of your waste. All that’s left should in theory be food waste and compostable scraps.","act_1":"#1 REFUSE","act_2":"#2 REDUCE","act_3":"#3 REUSE","act_4":"#4 RECYCLE","act_5":"#5 ROT","subtitle_msg":"Try to keep these rules in mind and to apply them in your everyday life.","next_btn":"Overview Consequences","next_examples":"Overview Actions"}}')
